html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Helvetica";
  font-size: 1.6rem;
  height: 100%;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: black;
  margin-left: 2rem;
}

a {
  text-decoration: none;
  text-transform: uppercase;
  color: white;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
  margin-left: 2rem;
}

h1 {
  font-size: 10rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 1.2rem;
}

.ocultar {
  display: none;
}

.seccion_carga_csv {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.seccion_carga_csv .container {
  width: 50%;
  height: 50%;
}
.seccion_carga_csv .container h1 {
  color: #333;
}

.eleccion {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contenedor_eleccion {
  width: 70%;
  height: 70%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}
.contenedor_eleccion a {
  text-decoration: none !important;
  color: #333;
  text-transform: none;
}

.contenedor_eleccion_db {
  cursor: pointer;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.contenedor_eleccion_db:hover {
  background-color: #00a99d;
}

.contenedor_eleccion_csv {
  cursor: pointer;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.contenedor_eleccion_csv:hover {
  background-color: #00a99d;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 4rem;
}
.title h3 {
  font-size: 2.5rem;
}

.contenedor_imagen {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contenedor_icono {
  width: 30rem;
  height: 30rem;
  background-color: transparent;
  background-image: url("../../../build/img/database.png");
  background-size: cover;
  /* Ajusta la imagen al tamaño del div */
  background-repeat: no-repeat;
  /* Evita que la imagen se repita */
}

.contenedor_icono2 {
  width: 30rem;
  height: 30rem;
  background-color: transparent;
  background-image: url("../../../build/img/csv.png");
  background-size: cover;
  /* Ajusta la imagen al tamaño del div */
  background-repeat: no-repeat;
  /* Evita que la imagen se repita */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

.video-container {
  align-content: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  overflow: hidden;
}

#video-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.contenedor_titulo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
  z-index: 1;
}
.contenedor_titulo span {
  color: #f9a602;
}

h1 {
  font-size: 10rem;
  color: #ffff;
}

h2 {
  font-size: 1.5rem;
  color: #00a99d;
}

.login-form-container {
  padding: 50px;
}
.login-form-container a {
  color: #f9a602;
}

.login-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 5px;
  background-color: #fff;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  width: 100%;
  padding: 10px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #3e3d3d;
}

p {
  margin-top: 1rem;
  font-size: 1.5rem;
}

.contenedor_mensajeBienvenida {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.mesajeBienvenida {
  width: 100%;
}
.mesajeBienvenida h2 {
  text-transform: uppercase;
  font-size: 8rem;
}
.mesajeBienvenida .conx {
  color: #f9a602;
}

.lienzo {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 20% 80%;
}

.metrics-board {
  height: 100%;
}

.board {
  min-height: 300px;
  height: 100%;
  background-color: #33333333;
}

.contenedor_crear_modelo {
  width: 80%;
  margin: 0 auto;
}
.contenedor_crear_modelo input {
  width: 20rem;
}
.contenedor_crear_modelo label {
  color: #fff;
  font-size: 1.5rem;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
}

/* Estilos para los elementos del grid */
.grid-item {
  padding: 20px;
  background-color: #33333353;
}

.modelo-div {
  border: 2px solid #333;
  border-radius: 5px;
  padding: 10px;
  margin: 4rem;
  max-width: 100%;
}
.modelo-div .btnCrearModelo {
  width: 20rem;
}

.texto_noModelos {
  font-size: 4rem;
  color: #3333331a;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

h5 {
  margin-top: 4rem;
  margin-left: 4rem;
  font-size: 4rem;
}

.contenedor_tabla_evaluar {
  height: 100%;
  width: 40%;
  top: 0;
  right: 0;
  position: absolute;
  background-color: #ccc;
}
.contenedor_tabla_evaluar h3 {
  margin-top: 2rem;
  font-size: 2rem;
  margin-left: 2rem;
  margin-bottom: 4rem;
}

.contenedor-tablasEvaluar {
  margin-left: 2rem;
}

.btn_siguientePrediccion {
  width: 20rem;
  margin-left: 2rem;
}

/* Estilos para el formulario */
#columnasTabla {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 10px;
}

#marcarDesmarcar {
  background-color: #008f82;
  color: white;
  border: none;
  cursor: pointer;
  width: 20rem;
}

#marcarDesmarcar:hover {
  background-color: #00a99d;
}

.tabla_Prediccion_Hecha table, .contenedor_tabla_atributos table {
  margin: 2rem auto;
}
.tabla_Prediccion_Hecha table, .contenedor_tabla_atributos table {
  border-collapse: collapse;
  width: 80%;
}
.tabla_Prediccion_Hecha th, .tabla_Prediccion_Hecha td, .contenedor_tabla_atributos th, .contenedor_tabla_atributos td {
  border: 1px solid #333;
  text-align: left;
  padding: 8px;
}
.tabla_Prediccion_Hecha th, .contenedor_tabla_atributos th {
  background-color: #f2f2f2;
}
.tabla_Prediccion_Hecha tr:nth-child(even), .contenedor_tabla_atributos tr:nth-child(even) {
  background-color: #f2f2f2;
}

.contenedor_tabla_atributos table {
  margin: 2rem auto;
}
.contenedor_tabla_atributos table {
  border-collapse: collapse;
  width: 50%;
}
.contenedor_tabla_atributos th, .contenedor_tabla_atributos td {
  border: 1px solid #333;
  text-align: left;
  padding: 8px;
}
.contenedor_tabla_atributos th {
  background-color: #f2f2f2;
}
.contenedor_tabla_atributos tr:nth-child(even) {
  background-color: #f2f2f2;
}

.contenedor_Opciones {
  display: flex;
  width: 100%;
  height: 8rem;
  align-items: center;
  justify-content: center;
}
.contenedor_Opciones button {
  background-color: #fff;
  color: #333;
  border: #333 solid 2px;
  max-width: 25rem;
  margin-left: 2rem;
}
.contenedor_Opciones button:hover {
  background-color: #333;
  color: #fff;
  border: #fff solid 2px;
  max-width: 25rem;
  margin-left: 2rem;
}

#dashboard {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  max-width: 90%;
  margin: 30px auto;
}

.widget {
  padding: 20px;
}
.widget h2 {
  font-size: 2.5rem;
