  width: 50%;
  position: fixed;
  background-color: #fff;
  border: #333 solid 2px;
}

.lista-tablas {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}

.lista-tablas li {
  padding: 5px 10px;
  background-color: #f0f0f086;
  border: 1px solid #ccc;
  margin-bottom: 5px;
  font-size: 14px;
}

.lista-tablas li:hover {
  background-color: #fff;
  cursor: pointer;
}

#map {
  width: 100%;
  height: 500px;
}

.minimize-button, .maximize-button, .close-button, .btn-add-table {
  width: 50px;
  height: 50px;
  float: right;
  cursor: pointer;
  border: none;
  background: none;
  font-weight: bold;
}

.tab {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: #f1f1f1;
  padding: 5px 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
}

.logout-warning {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.logout-warning h2 {
  margin: 0;
  color: #ff0000;
}

.logout-warning p {
  margin: 10px 0;
}

.logout-warning button {
  padding: 8px 15px;
  margin: 5px;
  border: none;
  border-radius: 3px;
  background-color: #00a99d;
  color: #fff;
  cursor: pointer;
}

.logout-warning button:hover {
  background-color: #008d81;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  /* Valor alfa para hacerla semi-transparente */
  z-index: 9999;
  /* Asegura que la capa se muestre por encima de otros elementos */
  pointer-events: auto;
  /* Deshabilita los eventos del mouse y del teclado */
}

.contenido_grafica {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10rem;
}

.btn-add {
  background-color: transparent;
  width: 50px;
}

.btn-add:hover {
  background-color: transparent;
}

.contenedor_seccion {
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.btnCrearModelo {
  width: 50px;
  margin: 2rem 2rem;
  align-items: center;
}
.btnCrearModelo p {
  color: #333;
  font-size: 1.5rem;
}

.contenedor_btn {
  display: flex;
  align-items: center;
  width: 20%;
  margin-left: 2rem;
  font-size: 1.5rem;
  color: #fff;
}

.item.selected {
  background-color: #fff;
  font-weight: bold;
}